import React from 'react'
import {
  Nav,
  NavItem,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem
} from 'reactstrap'
import { NavLink } from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.min.css'
import * as Icon from 'react-feather'
// import Logo from './assets/img/logo/logo-dark2.png';
import LogoDark from './assets/img/logo/LogoDark.png'
import LogoLight from './assets/img/logo/LogoLight.png'
import themeConfig from './configs/themeConfig'
import { connect } from 'react-redux'

const Logo = themeConfig.theme === 'dark' ? LogoDark : LogoLight

const tabs = [
  {
    route: '/assets/add',
    icon: <Icon.DollarSign size={20} className="mr-50" />,
    label: 'Add Assets'
  },
  {
    route: '/liability/add',
    icon: <Icon.Command size={20} className="mr-50" />,
    label: 'Add Liabilties'
  },
  {
    route: '/login',
    icon: <img src={Logo} style={{ width: '25px', height: '24px' }} />,
    label: 'Login'
  },
  {
    route: '/portfolio',
    icon: <Icon.Briefcase size={20} className="mr-50" />,
    label: 'View Portfolio'
  },
  {
    route: '/nominee/list',
    icon: <Icon.Users size={20} className="mr-50" />,
    label: 'Manage Nominees'
  }
]

const MyNav = ({ messages }) => {
  const mobileMenu = [
    {
      id: 'assets',
      title: messages?.asset ? messages?.asset : 'Assets',
      type: 'item',
      icon: <Icon.DollarSign size={25} />,
      permissions: ['admin', 'editor'],
      navLink: '/assets/add'
    },
    {
      id: 'Liabilties',
      title: messages?.liability ? messages?.liability : 'Liabilties',
      type: 'item',
      icon: <Icon.Command size={25} />,
      permissions: ['admin', 'editor'],
      navLink: '/liability/add'
    },
    {
      id: 'nominee',
      title: messages?.nominees ? messages?.nominees : 'Nominees',
      type: 'item',
      icon: <Icon.Users size={25} />,
      permissions: ['admin', 'editor'],
      navLink: '/nominee/list'
    },
    {
      id: 'portfolio',
      title: messages?.portfolio ? messages?.portfolio : 'Portfolio',
      type: 'item',
      icon: <Icon.Briefcase size={25} />,
      permissions: ['admin', 'editor'],
      navLink: '/portfolio'
    },
    {
      id: 'pwd',
      title: messages?.vault ? messages?.vault : 'Vault',
      type: 'item',
      icon: <Icon.Lock size={25} />,
      permissions: ['admin', 'editor'],
      navLink: '/vault/password'
    },
    {
      id: 'vault',
      title: messages?.documents ? messages?.documents : 'Documents',
      type: 'item',
      icon: <Icon.Archive size={25} />,
      permissions: ['admin', 'editor'],
      navLink: '/vault/document'
    },
    {
      id: 'diary',
      title: messages?.diary ? messages?.diary : 'Secret Diary',
      type: 'item',
      icon: <Icon.FileText size={25} />,
      permissions: ['admin', 'editor'],
      navLink: '/diary'
    },
    {
      id: 'dailyspends',
      title: messages?.spends ? messages?.spends : 'Daily Spends',
      type: 'item',
      icon: <Icon.FileMinus size={25} />,
      permissions: ['admin', 'editor'],
      navLink: '/spends'
    },
    {
      id: 'contact',
      title: this?.props?.sender?.heading
        ? this?.props?.sender?.heading
        : 'Sender',
      type: 'item',
      icon: <Icon.Share size={20} />,
      permissions: ['admin', 'editor'],
      navLink: '/contactUs'
    }
  ]
  return (
    <div>
      <nav
        className="navbar fixed-bottom navbar-dark"
        role="navigation"
        style={{ backgroundColor: '#21212a' }}
      >
        <Nav className="w-100" style={{ backgroundColor: '#21212a' }}>
          <div
            className=" d-flex flex-row justify-content-around w-100"
            style={{ backgroundColor: '#21212a' }}
          >
            {tabs.map((tab, index) => {
              if (tab.label === 'Login') {
                return (
                  <NavItem key={`tab-${index}`}>
                    <UncontrolledDropdown nav inNavbar direction="up">
                      <DropdownToggle nav caret>
                        {tab.icon}
                      </DropdownToggle>
                      <DropdownMenu
                        flip
                        left
                        style={{
                          width: '322px',
                          marginLeft: '-142px',
                          border: 'solid 5px'
                        }}
                      >
                        {mobileMenu.map((item, key) => {
                          return (
                            <NavLink to={item.navLink} id={item.id}>
                              <DropdownItem>
                                <span
                                  style={{
                                    fontWeight: 'bolder',
                                    fontSize: '20px'
                                  }}
                                >
                                  {item.icon}
                                  {'  '}
                                  {item.title}
                                </span>
                              </DropdownItem>
                            </NavLink>
                          )
                        })}

                        <DropdownItem divider />
                        <DropdownItem
                          style={{
                            fontWeight: 'bolder',
                            fontSize: '20px'
                          }}
                        >
                          Log Out
                        </DropdownItem>
                      </DropdownMenu>
                    </UncontrolledDropdown>
                  </NavItem>
                )
              } else {
                return (
                  <NavItem key={`tab-${index}`}>
                    <NavLink
                      to={tab.route}
                      className="nav-link"
                      activeClassName="active"
                    >
                      <div className="row d-flex flex-column justify-content-center align-items-center">
                        {tab.icon}
                      </div>
                    </NavLink>
                  </NavItem>
                )
              }
            })}
          </div>
        </Nav>
      </nav>
    </div>
  )
}
const mapStateToProps = (state) => {
  const { customizer } = state?.customizer
  return {
    messages: customizer?.language?.Dashboard,
    sender: customizer?.language?.Sender
  }
}

export default connect(mapStateToProps)(MyNav)
